<template>
  <div class="g-login-box">
    <van-nav-bar
      title="你点我改"
      left-arrow
      @click-left="toPageReturn"
    />
    <van-form>
      <van-field
        name="姓名"
        label="姓名"
        placeholder="姓名"
        :rules="[{ required: false, message: '请填写姓名' }]"
      />
      <van-field
        name="手机号"
        label="手机号"
        placeholder="手机号"
        :rules="[{ required: false, message: '请填写手机号' }]"
      />
      <van-field
        name="建议分类"
        label="建议分类"
        placeholder="建议分类"
      >
        <template #input>
          <span class="u-tag" @click="selectTag('街道建设')" :data-active="state.tagList.includes('街道建设')">街道建设</span>
          <span class="u-tag" @click="selectTag('文明建设')" :data-active="state.tagList.includes('文明建设')">文明建设</span>
          <span class="u-tag" @click="selectTag('农业生产')" :data-active="state.tagList.includes('农业生产')">农业生产</span>
          <span class="u-tag" @click="selectTag('其他')" :data-active="state.tagList.includes('其他')">其他</span>
        </template>
      </van-field>
      <van-field
        rows="2"
        autosize
        label="建议和意见"
        type="textarea"
        maxlength="200"
        placeholder="请输入你的建议和意见（最多 200 字）"
        show-word-limit
      />
      <van-field
        v-model="state.fieldValue"
        is-link
        readonly
        label="地区"
        placeholder="请选择所在地区"
        @click="state.show = true"
      />
      <van-popup v-model:show="state.show" round position="bottom">
        <van-cascader
          v-model="state.cascaderValue"
          title="请选择所在地区"
          :options="options"
          @close="state.show = false"
          @finish="onFinish"
        />
      </van-popup>
      <div style="margin: 16px;">
        <van-button round block type="primary" native-type="submit" @click="submit">
          提交
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, ref } from 'vue'
import router from './../router'
import { Dialog, Toast } from 'vant'

export default defineComponent({
  setup () {
    const tagList: string[] = []
    const state = reactive({
      show: false,
      fieldValue: '',
      cascaderValue: '',
      value: '',
      showPicker: false,
      tagList: tagList
    })
    const options = [
      {
        text: '重庆市',
        value: '330000',
        children: [{
          text: '九龙坡区',
          value: '330100',
          children: [{
            text: '石桥铺',
            value: '330140'
          }]
        }]
      },
      {
        text: '江苏省',
        value: '320000',
        children: [{ text: '南京市', value: '320100' }]
      }
    ]
    // 全部选项选择完毕后，会触发 finish 事件
    const onFinish = ({ selectedOptions }: any) => {
      state.show = false
      state.fieldValue = selectedOptions.map((option: any) => option.text).join('/')
    }
    const toPage = () => router.push('/order')
    const toPageReturn = () => router.go(-1)
    const submit = () => {
      Dialog.confirm({
        title: '确认',
        message: '是否确认该意见已处理？'
      })
    }
    const selectTag = (name: string) => {
      if (state.tagList.includes(name)) state.tagList.splice(state.tagList.findIndex(item => item === name), 1)
      else state.tagList.push(name)
    }

    return {
      state,
      toPage,
      submit,
      toPageReturn,
      selectTag,
      options,
      onFinish
    }
  }
})
</script>

