
import { defineComponent, reactive, ref } from 'vue'
import router from './../router'
import { Dialog, Toast } from 'vant'

export default defineComponent({
  setup () {
    const tagList: string[] = []
    const state = reactive({
      show: false,
      fieldValue: '',
      cascaderValue: '',
      value: '',
      showPicker: false,
      tagList: tagList
    })
    const options = [
      {
        text: '重庆市',
        value: '330000',
        children: [{
          text: '九龙坡区',
          value: '330100',
          children: [{
            text: '石桥铺',
            value: '330140'
          }]
        }]
      },
      {
        text: '江苏省',
        value: '320000',
        children: [{ text: '南京市', value: '320100' }]
      }
    ]
    // 全部选项选择完毕后，会触发 finish 事件
    const onFinish = ({ selectedOptions }: any) => {
      state.show = false
      state.fieldValue = selectedOptions.map((option: any) => option.text).join('/')
    }
    const toPage = () => router.push('/order')
    const toPageReturn = () => router.go(-1)
    const submit = () => {
      Dialog.confirm({
        title: '确认',
        message: '是否确认该意见已处理？'
      })
    }
    const selectTag = (name: string) => {
      if (state.tagList.includes(name)) state.tagList.splice(state.tagList.findIndex(item => item === name), 1)
      else state.tagList.push(name)
    }

    return {
      state,
      toPage,
      submit,
      toPageReturn,
      selectTag,
      options,
      onFinish
    }
  }
})
